import wx from 'weixin-js-sdk'
import { mapGetters,mapActions } from 'vuex'
  export default {
      name: "voucheretails",
    mounted(){
        document.title = '代金券详情'
    },
    computed:{
      ...mapGetters(['djqDtail'])
    },
    created(){
      if(this.$route.query.token){
        localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
      }
      if(this.$route.query.refreshToken){
        localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
      }
      if(this.$route.query.id){
        this.getdjqDtail({data:{productPromoCodeId:this.$route.query.id},method:this.getdjqDtail,this:this})
      }
      else{
        let t0= decodeURIComponent(this.$route.query.cs) || '{couponName:\'返回失败\'}'
        console.log(t0)
        this.getdjqDtail1(JSON.parse(t0))
      }
    },
    methods:{
      ...mapActions(['getdjqDtail','getdjqDtail1']),
      lisy(s){
       if(this.$route.query.csi=='1'){
        if(s.applyScope == 'S' && s.isInner == 'N'){
          wx.miniProgram.navigateTo({
            url: '/pages/shopIndex/index?id=' + s.productStoreId
          })
        }
        else{
          wx.miniProgram.switchTab({
            url: '/pages/home/index'
          })
        }
      }
      }
    }
  }



